.row{
    margin-left: 20px;
    color: white;
}

.row_posters{ 
    display:flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar { 
    display: none;
}
/* nessa propriedade falo para todos os navegadores esconderem a scrollbar, chrome,firefox, edge,safari */
.row_poster{ 
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 550ms;

}

.row_poster:hover{ 
    transform: scale(1.08);
    opacity: 1 ;
}

.row_posterLarge{ 
    max-height: 250px;
}

.row_posterLarge:hover{ 
    transform: scale(1.09)
}